@import '../base';

%abs-w-h-100 {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.popup {
  position: fixed;
  @extend %abs-w-h-100;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  padding: 20px;
  z-index: 1060;
  overflow: auto;

  &-backdrop {
    @extend %abs-w-h-100;
    position: fixed;
    background: rgba($black, 0.65);
    animation: fadeIn 400ms 1 ease;
    animation-fill-mode: forwards;
    opacity: 0;
  }

  &-shape {
    position: relative;
    flex: 1;
    max-width: rem(870px);
    z-index: 1;
    animation: fromUp 650ms 1 cubic-bezier(0.445, 0.05, 0.55, 0.95);
    animation-fill-mode: forwards;

    & > svg {
      position: absolute;
      z-index: -1;
      @extend %abs-w-h-100;
    }
  }

  &-close {
    position: fixed;
    bottom: 30px;
    left: 50%;
    margin-left: -25px;
    background: none;
    border: none;
    width: 50px;
    padding: 0;
    z-index: 1;
    opacity: 0.85;
    animation: comeWithRotatation 500ms 420ms 1
      cubic-bezier(0.445, 0.05, 0.55, 0.95);
    animation-fill-mode: forwards;
    transform: rotate(-180deg) scale(0);
    filter: drop-shadow(0 2px 4px rgba($black, 0.5));
  }

  &-content {
    position: absolute;
    @extend %abs-w-h-100;
    right: 0;
    margin: 0 auto;
    max-width: 80%;

    &-body {
      margin-right: 20px;
    }

    &-img {
      width: 50%;
    }

    &-media {
      width: 70%;
      max-width: 450px;
      height: 230px;
      object-fit: cover;
    }

    &-title {
      font-size: 40px;
    }

    &-sentence {
      font-size: 20px;
    }
  }
}

@keyframes fromUp {
  0% {
    transform: translateY(-120%);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes comeWithRotatation {
  to {
    transform: rotate(0deg) scale(1);
  }
}
