@import '../base.scss';

.footer {
  padding: rem(150px) 0 rem(30px);
  margin-top: rem(-150px);
  background: repeating-linear-gradient(
    -60deg,
    lighten($black, 12),
    lighten($black, 12) 1px,
    $black 1px,
    $black 7px
  );

  &-link {
    transition: color 200ms ease;

    &:hover {
      text-decoration: none;
      color: $white;
    }
  }

  &-navigation {
    a,
    span {
      display: inline-block;
      color: $color-orange;
      transition: color 200ms ease-in-out;
      padding: rem(4px) rem(3px);
    }

    a:hover,
    a:focus,
    a:active:focus {
      color: $white;
      text-decoration: none;
    }
  }

  &-query-form {
    &-input {
      display: block;
      width: 100%;
      color: $gray-lighten;
      margin-bottom: rem(10px);
      background: transparent;
      border: 1px solid $color-orange;
      padding: rem(4px) rem(8px);

      &:focus {
        outline: none;
        border-color: $gray-lighten;
      }

      @include input-placeholder {
        color: $color-orange;
      }
    }

    &-btn {
      display: block;
      margin: rem(20px) auto 0;
      background: $color-orange;
      border: 1px solid $color-orange;
      color: $white;
      font-size: rem(27px);
      padding: rem(4px) rem(10px);
      border-radius: rem(5px);
      transition: background 200ms ease, border-color 200ms ease;

      &:hover,
      &:focus {
        background: $color-pink;
        border-color: $color-pink;
      }

      &:focus,
      &:active,
      &:active:focus {
        outline: none;
        box-shadow: 0 0 0 3px rgba($color-orange, 0.5);
      }
    }
  }

  &-contact {
    color: $color-orange;
    font-size: rem(23px);

    a {
      color: inherit;
    }

    small {
      font-size: 0.5em;
    }
  }
}
