@import '../base.scss';

.navigation {
  display: flex;

  &-container {
    position: absolute;
    left: 0;
    right: 0;
    top: auto;
    margin-top: 40px;
    padding: 80px 30px;
    z-index: 10;
    transition: transform 300ms ease, visibility 300ms ease;
    transform-origin: top;
    transform: scaleY(0);
    visibility: hidden;

    &.is-open {
      transform: scaleY(1);
      visibility: visible;
    }

    & > svg {
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      z-index: -1;
    }
  }

  &-item {
    margin: 0 20px;
  }

  &-link {
    display: block;
    border: 1px solid $color-red;
    color: $color-red;
    margin: 0 rem(5px);
    padding: rem(10px) rem(20px);
    transition: background 150ms ease, color 150ms ease;

    &:hover {
      color: $white;
      background: $color-red;
      text-decoration: none;
    }

    &-item {
      font-size: 18px;
      display: block;
      color: $white;
      padding: 8px 0;
      transition: color 200ms ease-in-out;

      &:hover {
        text-decoration: none;
        color: $black;
      }
    }
  }

  &-content {
    font-size: 18px;
    color: $white;
    max-width: rem(750px);
    line-height: 1.5em;
  }
}
