@import '../base.scss';

.testimonials {
  &-section {
    position: relative;
    z-index: 1;
    margin-top: rem(-340px);
    background: url(../../assets/images/orange-shape-bg.png) no-repeat top
      center/cover;
    min-height: rem(550px);
    padding-top: rem(150px);
    display: flex;
    align-items: center;
  }

  &-slider {
    margin: 0 auto;
    position: relative;

    $w: 185px;

    &-circle {
      position: absolute;
      width: rem($w);
      height: rem($w);
      border-radius: 50%;
      top: 50%;
      background: $color-orange;
      box-shadow: 1px 1px 2px 6px $color-orange-light;
      display: flex;
      align-items: center;
      justify-content: center;
      color: $white;
      font-size: rem(20px);

      &.left {
        transform: translateY(-50%) scaleX(1.1) scaleY(0.95) skewX(12deg)
          skewY(-8deg);
        left: rem(-$w / 2);

        & > span {
          transform: scaleX(1) scaleY(1) skewX(-12deg) skewY(8deg);
        }
      }

      &.right {
        transform: translateY(-50%) scaleX(1.1) scaleY(0.95) skewX(-12deg)
          skewY(8deg);
        right: rem(-$w / 2);

        & > span {
          transform: scaleX(1) scaleY(1) skewX(12deg) skewY(-8deg);
        }
      }
    }

    &-body {
      background: $white;
      margin: 0 auto;
      padding: rem(17px) rem(143px);
    }

    &-line {
      color: $color-orange;
      margin: 0;
    }

    &-byline {
      color: $black;
      margin: 0;
      text-align: right;
      font-size: rem(18px);
    }
  }
}
