@import '../base.scss';

.bubbles-section {
  padding: rem(75px) 0 0;

  &.top-section {
    position: relative;
    background: linear-gradient(to bottom, $gray-lighten 70%, transparent 70%);
    z-index: 1;
    border-radius: 0 0 0 20%;

    &::after {
      position: absolute;
      content: '';
      bottom: -30px;
      left: 0;
      width: 100%;
      z-index: -1;
      height: rem(291px);
      background: url(../../assets/images/bottom-gray-shape.png) no-repeat top
        center/cover;
    }
  }

  &.bottom-section {
    margin-top: rem(-170px);
    margin-bottom: rem(-70px);
    padding: rem(275px) 0 rem(120px);

    background: repeating-linear-gradient(
      -60deg,
      rgba($black, 0.065),
      rgba($black, 0.065) 1px,
      $white 1px,
      $white 7px
    );
  }
}
