@import '../base.scss';
@import './btn.scss';

.our-client-section {
  position: relative;
  background-color: $black;
  padding: rem(30px) 0 rem(220px);
  z-index: 1;

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: rem(-171px);
    left: 0;
    width: 100%;
    height: rem(181px);
    background: url(../../assets/images/bg-wave-black.png) no-repeat top
      center/cover;
  }

  h2 {
    font-size: rem(21px);
    color: $gray-800;
    margin-bottom: 1em;
  }
}
